import React from 'react'

const subtitles = [
  <span>to have a <span className="hashtag">#FirstDate</span></span>,
  <span>to have fun <span className="hashtag">#MakingOut</span></span>,
  <span>to have <span className="hashtag">#SexyTime</span></span>,
]

export default class PanelTitle extends React.Component {
  constructor() {
    super()
    this.state = {
      subtitleIndex: 0,
    }
    this.subtitleTimeout = null
  }

  componentDidMount() {
    this.changeSubtitleIndex()
  }

  componentWillUnmount() {
    this.subtitleTimeout && window.clearTimeout(this.subtitleTimeout)
  }

  changeSubtitleIndex = () => {
    const { subtitleIndex } = this.state
    this.subtitleTimeout && window.clearTimeout(this.subtitleTimeout)

    this.setState({
      subtitleIndex: subtitleIndex >= 2 ? 0 : subtitleIndex +1,
    }, () => {
      this.subtitleTimeout = window.setTimeout(this.changeSubtitleIndex, 3000)
    })
  }

  render() {
    const { subtitleIndex } = this.state
    const text = subtitles[subtitleIndex]

    return (
      <div className="panelTitle">
        <span>Love</span>Nest<span className="subtitle"> - find and rate places {text}</span>
      </div>
    )
  }
}



