import React from 'react'
import PropTypes from 'prop-types'

import DetailsForm from '../DetailsForm'
import LocationForm from '../LocationForm'

import { addLovenestReview } from '../../utils/algolia'
import { CheckSvg } from '../../utils/svg'

const defaultState = {
  step: 1,
  location: undefined,
  submitting: false,
  showSuccess: false,
}

export default class PostForm extends React.Component {
  static propTypes = {
    getCenter: PropTypes.func,
    fetchGoogleMap: PropTypes.func,
  }

  constructor() {
    super()
    this.state = { ...defaultState }
  }

  onSubmit = () => {
    if (!this.detailsForm.validate()) return

    const { location } = this.state
    this.setPlacesService()

    this.setState({
      submitting: true,
    })
    const { description, place_id } = location
    this.placesService.getDetails({
      placeId: place_id,
    }, (res) => {
      const { formatted_address, geometry } = res
      const { location } = geometry
      const geoloc = {
        lat: location.lat(),
        lng: location.lng(),
      }
      // a bit hack to do this so hasError key is not included in data
      const { hasError, ...detailsData } = this.detailsForm.getState()
      const data = {
        _geoloc: geoloc,
        formattedAddress: formatted_address,
        location: geoloc,
        placeId: place_id,
        description,
        query: this.locationForm.getQuery(),
        ...detailsData,
        deleted_at: null,
      }
      addLovenestReview(data).then(() => {
        this.setState({
          showSuccess: true,
        })
      })
    })
  }

  setDetailsForm = (c) => {
    this.detailsForm = c
  }

  setLocationForm = (c) => {
    this.locationForm = c
  }

  setPlacesService = () => {
    if (this.placesService) return

    const { fetchGoogleMap } = this.props
    this.placesService = new window.google.maps.places.PlacesService(fetchGoogleMap())
  }

  goNext = () => {
    const { step } = this.state
    if (step >= 2) return

    this.setState({
      step: step +1,
    })
  }

  goBack = () => {
    const { step } = this.state
    if (step <= 1) return

    this.setState({
      step: step -1,
    })
  }

  selectLocation = (location) => {
    this.setState({
      location,
    }, () => {
      this.goNext()
    })
  }

  reset = () => {
    this.setState(defaultState)
  }

  renderActions = () => {
    const { step, submitting } = this.state
    if (step === 1) {
      return (
        <div className="actions">
        </div>
      )
    }

    const onSubmit = submitting ? () => {} : this.onSubmit
    return (
      <div className="actions">
        <button className="clear-btn" onClick={this.goBack}>Back</button>
        <button className="main-btn" onClick={onSubmit}>
          {submitting ? '. . .' : 'Submit'}
        </button>
      </div>
    )
  }

  renderForm = () => {
    const { getCenter, fetchGoogleMap } = this.props
    const { location, step } = this.state
    const description = location && location.description

    return (
      <div>
        <LocationForm
          ref={this.setLocationForm}
          getCenter={getCenter}
          fetchGoogleMap={fetchGoogleMap}
          selectLocation={this.selectLocation}
          show={step === 1}
        />
        <DetailsForm
          ref={this.setDetailsForm}
          locationDescription={description}
          show={step === 2}
        />
      </div>
    )
  }

  renderSuccess = () => {
    return (
      <div className="success">
        <div>Success!</div>
        <div>
          <CheckSvg
            height={48}
            width={48}
            fill="#53b087"
          />
        </div>
        <div>
          <button className="main-btn" onClick={this.reset}>Submit another review</button>
        </div>
      </div>
    )
  }

  render() {
    const { showSuccess } = this.state

    if (showSuccess) return (
      <div className="postForm">{this.renderSuccess()}</div>
    )

    return (
      <div className="postForm">
        {this.renderForm()}
        {this.renderActions()}
      </div>
    )
  }
}



