import React from "react"
import PropTypes from 'prop-types'
import { compose, withProps } from "recompose"
import { withGoogleMap, GoogleMap, Marker } from "react-google-maps"
import { MAP } from 'react-google-maps/lib/constants'
import store from 'store'

import { mapStyles } from './config'

const defaultOptions = {
  fullscreenControl: false,
  mapTypeControlOptions: {
    mapTypeIds: []
  },
  maxZoom: 17,
  minZoom: 14,
  streetViewControl: false,
  styles: mapStyles,
  zoom: 14,
}

const ratingIcon = {
  bad: 'https://imgur.com/Zdg4kdG.png',
  good: 'https://imgur.com/voIngul.png',
  awesome: 'https://imgur.com/jvklg1Z.png',
}

const ComposedMap = compose(
  withProps({
    googleMapURL: "https://maps.googleapis.com/maps/api/js?v=3.exp&libraries=geometry,drawing,places",
    loadingElement: <div style={{ height: `100%` }} />,
    containerElement: <div style={{ height: `100%` }} />,
    mapElement: <div style={{ height: `100%` }} />,
  }),
  withGoogleMap
)((props) => {
  const { center, markers, setMap, onCenterChanged } = props
  return (
    <GoogleMap
      ref={setMap}
      center={center}
      defaultOptions={defaultOptions}
      onCenterChanged={onCenterChanged}
    >
      {markers}
    </GoogleMap>
  )
})

export default class Map extends React.Component {
  static propTypes = {
    center: PropTypes.object,
    reviews: PropTypes.array,
    onCenterChanged: PropTypes.func.isRequired,
    onSelectReview: PropTypes.func.isRequired,
    selectedReview: PropTypes.object,
  }

  constructor() {
    super()
    this.state = {
    }
    this.map = undefined
    this.mouseOverObjectId = undefined
  }

  onCenterChanged = (res) => {
    const { onCenterChanged } = this.props

    const coord = this.map.getCenter()
    onCenterChanged({
      lat: coord.lat(),
      lng: coord.lng(),
    })
  }

  setMap = (c) => {
    this.map = c
    window.google.maps.event.addDomListener(window, 'resize', () => {
      const innerMap = this.map.context[MAP]
      const center = innerMap.getCenter()
      window.google.maps.event.trigger(innerMap, 'resize')
      innerMap.setCenter(center)
    })

    const currentCenter = store.get('currentCenter')
    const innerMap = this.map.context[MAP]
    if (innerMap && currentCenter) {
      innerMap.setCenter(currentCenter);
    }
  }

  fetchGoogleMap = () => {
    return this.map.context[MAP]
  }

  updateCenter = (center) => {
    const innerMap = this.map.context[MAP]
    innerMap.setCenter(center)
  }

  makeMarkers = () => {
    const { reviews, onSelectReview, selectedReview } = this.props
    const selectedObjId = selectedReview && selectedReview.objectID
    if (!reviews) return []

    return reviews.map((obj) => {
      const { location, rating, objectID } = obj
      const isSelected = selectedObjId === objectID

      return (
        <Marker
          key={objectID}
          opacity={isSelected ? 1 : 0.8}
          onClick={() => onSelectReview(obj, true)}
          onMouseOver={() => {
            this.mouseOverObjectId = objectID
            this.forceUpdate()
          }}
          position={location}
          icon={ratingIcon[rating]}
          zIndex={this.mouseOverObjectId === objectID ? 9000 : 1}
        />
      )
    })
  }

  render() {
    const { center } = this.props

    return (
      <div className="mapWrapper">
        <ComposedMap
          center={center}
          setMap={this.setMap}
          markers={this.makeMarkers()}
          onCenterChanged={this.onCenterChanged}
        />
      </div>
    )
  }
}



