import algoliasearch from 'algoliasearch'
import Promise from 'bluebird'

const client = algoliasearch('2Y7SH4BKWJ', '6eb48c3e97639ef3ef6f1c8728b5e00f', {
  protocol: 'https:'
})

const debugAlgolia = process.env.NODE_ENV === 'development'

export const lovenestReviewsIndex = client.initIndex(
  debugAlgolia ? 'lovenest_reviews_development' : 'lovenest_reviews_production'
)

const addDefaultPostAttrs = (data) => {
  const now = new Date()
  const location = window.location
  const { referrer } = document
  const { host, search } = location
  return {
    origin: host,
    referrer,
    search,
    path: location.pathname,
    createdAt: now,
    updatedAt: now,
    ...data,
  }
}

export const postToIndex = (index, data) => {
  const postData = addDefaultPostAttrs(data)
  if (debugAlgolia) {
    console.log(`DEBUG - Posting to ${index.indexName}: `, postData)
    return Promise.resolve()
  }
  return index.addObject(postData)
}

export const addLovenestReview = (data) => {
  return postToIndex(lovenestReviewsIndex, data)
}



