import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import { SearchSvg } from '../../utils/svg'

const geoNavigator = window.navigator.geolocation

export default class LocationForm extends React.Component {
  static propTypes = {
    getCenter: PropTypes.func,
    fetchGoogleMap: PropTypes.func,
    query: PropTypes.string,
    selectLocation: PropTypes.func,
    show: PropTypes.bool,
  }

  constructor() {
    super()
    this.state = {
      locationFocus: false,
      query: '',
      autocompleteResults: [],
      locating: false,
    }
    this.queryTimeout = null
  }

  componentDidMount() {
    const { query } = this.props

    this.setAutocomplete()
    if (query && query !== '') {
      this.updateQuery(query)
    }
  }

  componentWillUnmount() {
    this.clearQueryTimeout()
  }

  onBlur = () => {
    this.setState({
      locationFocus: false,
    })
  }

  onFocus = () => {
    this.setState({
      locationFocus: true,
    })
  }

  onQueryChange = (e) => {
    const { value } = e.target
    this.updateQuery(value)
  }

  setAutocomplete = () => {
    if (this.autocomplete) return

    this.autocomplete = new window.google.maps.places.AutocompleteService()
  }

  clearQueryTimeout = () => {
    this.queryTimeout && window.clearTimeout(this.queryTimeout)
  }

  updateQuery = (value) => {
    this.clearQueryTimeout()

    this.setState({
      query: value,
    }, () => {
      this.queryTimeout = window.setTimeout(() => {
        this.fetchAutocomplete()
        this.clearQueryTimeout()
      }, 250)
    })
  }

  fetchAutocomplete = () => {
    const { getCenter } = this.props
    const { query } = this.state
    if (!query || query === '') {
      this.setState({
        autocompleteResults: [],
      })
      return
    }

    this.autocomplete.getQueryPredictions({
      input: query,
      location: new window.google.maps.LatLng(getCenter()),
      radius: 49999,
      types: ['address'],
    }, (res) => {
      this.setState({
        autocompleteResults: res,
      })
    })
  }

  fetchCurrentLocation = () => {
    this.setState({
      locating: true,
    })
    geoNavigator.getCurrentPosition((pos) => {
      const { latitude, longitude } = pos.coords
      const geocoder = new window.google.maps.Geocoder()
      geocoder.geocode({
        location: { lat: latitude, lng: longitude },
      }, (res, status) => {
        if (status === 'OK') {
          const first = res[0]
          const { formatted_address } = first
          this.updateQuery(formatted_address)
        }
        this.setState({
          locating: false,
        })
      })
    });
  }

  getQuery = () => {
    return this.state.query
  }

  renderLocationInput = () => {
    const { locationFocus, query } = this.state

    return (
      <div className={classNames("locationInput", { focused: locationFocus })}>
        <input
          type="text"
          name="location"
          onChange={this.onQueryChange}
          onFocus={this.onFocus}
          onBlur={this.onBlur}
          value={query}
          placeholder="Enter an address or name"
        />
        <button className="search">
          <SearchSvg
            height={18}
            width={18}
            stroke="#000"
            strokeWidth={2}
          />
        </button>
      </div>
    )
  }

  renderResultSearch = (result) => {
    const { description } = result
    return (
      <button onClick={() => this.updateQuery(description)}>
        <SearchSvg
          height={14}
          width={14}
          stroke="#000"
          strokeWidth={2}
        />
        {description}
      </button>
    )
  }

  renderResultSelect = (result) => {
    const { selectLocation } = this.props
    const { description } = result
    return (
      <button onClick={() => selectLocation(result)}>
        {description}
      </button>
    )
  }

  renderResults = () => {
    const { autocompleteResults, query } = this.state
    const isEmpty = autocompleteResults.length === 0

    return (
      <div className="results">
        {!isEmpty && <div className="directions">Pick a location</div>}
        {
          autocompleteResults.map((result) => {
            const { description, place_id } = result
            if (description === query && !place_id) return null

            return (
              <div key={place_id || description} className="result">
                {place_id ? this.renderResultSelect(result) : this.renderResultSearch(result)}
              </div>
            )
          })
        }
      </div>
    )
  }

  renderGeolocate = () => {
    const { locating } = this.state
    const onClick = locating ? () => {} : this.fetchCurrentLocation

    return (
      <div className="geolocate">
        <button className="clear-btn" onClick={onClick}>
          {locating ? 'Locating . . .' : 'Search current location'}
        </button>
      </div>
    )
  }

  render() {
    const { show } = this.props

    return (
      <div className={classNames("locationForm", { show })}>
        {this.renderLocationInput()}
        {this.renderResults()}
        {geoNavigator && this.renderGeolocate()}
      </div>
    )
  }
}



