import React from 'react'
import PropTypes from 'prop-types'

const defaultSvgProps = {
  fill: '#333',
  height: 48,
  stroke: '#333',
  width: 48,
  strokeWidth: 2,
}

const svgPropTypes = {
  fill: PropTypes.string,
  height: PropTypes.number,
  stroke: PropTypes.string,
  width: PropTypes.number,
  onClick: PropTypes.func,
  strokeWidth: PropTypes.number,
}

export const SvgWrapper = ({ children, width, height, fill, ...rest }) => {
  fill = fill || '#000'
  return (
    <svg
      version="1.1" xmlns="http://www.w3.org/2000/svg"
      xmlnsXlink="http://www.w3.org/1999/xlink"
      x="0px"
      y="0px"
      viewBox="0 0 48 48"
      xmlSpace="preserve"
      height={height}
      width={width}
      {...rest}
    >
      <g className="nc-icon-wrapper" fill={fill}>
        {children}
      </g>
    </svg>
  )
}

export const ThumbsDownSvg = ({ fill, height, width }) => {
  return (
    <SvgWrapper height={height} width={width}>
      <path data-color="color-2" fill={fill} d="M9,1H2C1.4,1,1,1.4,1,2v22c0,0.6,0.4,1,1,1h7V1z" />
      <path fill={fill} d="M11,25l6,21.3c0.1,0.4,0.5,0.7,1,0.7c3.9,0,7-3.1,7-7V29h14c2,0,4-0.9,5.3-2.4c1.3-1.6,1.9-3.6,1.6-5.6 l-2.2-14C43.2,3.5,40.3,1,36.9,1H12h-1V25z" />
    </SvgWrapper>
  )
}

ThumbsDownSvg.defaultProps = defaultSvgProps
ThumbsDownSvg.propTypes = svgPropTypes

export const ThumbsUpSvg = ({ fill, height, width }) => {
  return (
    <SvgWrapper height={height} width={width}>
      <path data-color="color-2" fill={fill} d="M9,23H2c-0.6,0-1,0.4-1,1v22c0,0.6,0.4,1,1,1h7V23z" />
      <path fill={fill} d="M11,47h1h24.9c3.5,0,6.4-2.5,6.9-5.9l2.2-14c0.3-2-0.3-4.1-1.6-5.6C43,19.9,41.1,19,39,19H25V8 c0-3.9-3.1-7-7-7c-0.5,0-0.8,0.3-1,0.7L11,23V47z" />
    </SvgWrapper>
  )
}

ThumbsUpSvg.defaultProps = defaultSvgProps
ThumbsUpSvg.propTypes = svgPropTypes

export const FlameSvg = ({ fill, height, width }) => {
  return (
    <SvgWrapper height={height} width={width}>
      <path fill={fill} d="M24.7,3.3c-0.4-0.4-1-0.4-1.4,0C22.6,4,7,19.8,7,30c0,10.6,8.6,17,17,17s17-6.4,17-17 C41,19.8,25.4,4,24.7,3.3z M24,37c-1.8,0-7-1.3-7-7c0.1-3.1,4.8-9.4,6.2-11.3c0.2-0.3,0.5-0.4,0.8-0.4c0.3,0,0.6,0.1,0.8,0.4 c1.4,1.9,6.1,8.3,6.2,11.3C31,35.7,25.8,37,24,37z" />
    </SvgWrapper>
  )
}

FlameSvg.defaultProps = defaultSvgProps
FlameSvg.propTypes = svgPropTypes

export const ColoredFlameSvg = ({ fill, height, width, stroke, strokeWidth }) => {
  return (
    <SvgWrapper height={height} width={width}>
      <path fill="#E86C60" d="M24,47c-8.35645,0-17-6.35889-17-17C7,19.83008,22.625,3.96582,23.29004,3.29541 c0.37598-0.37793,1.04395-0.37793,1.41992,0C25.375,3.96582,41,19.83008,41,30C41,40.64111,32.35645,47,24,47z" />
      <path fill="#EFD358" d="M24,36c-1.55664,0-6-1.10254-6-6c0.0918-1.91602,2.56348-6.17676,5.99316-10.71875 c3.4375,4.55469,5.91602,8.82324,6.00684,10.74023C30,34.89746,25.55664,36,24,36z" />
    </SvgWrapper>
  )
}

ColoredFlameSvg.defaultProps = defaultSvgProps
ColoredFlameSvg.propTypes = svgPropTypes

export const SearchSvg = ({ fill, height, width, stroke, strokeWidth }) => {
  return (
    <SvgWrapper height={height} width={width}>
      <line data-color="color-2" fill="none" stroke={stroke} strokeWidth={strokeWidth} strokeLinecap="round" strokeMiterlimit="10" x1="44" y1="44" x2="38" y2="38" strokeLinejoin="round" />
      <circle fill="none" stroke={stroke} strokeWidth={strokeWidth} strokeLinecap="round" strokeMiterlimit="10" cx="20" cy="20" r="18" strokeLinejoin="round" />
      <path data-color="color-2" fill="none" stroke={stroke} strokeWidth={strokeWidth} strokeLinecap="round" strokeMiterlimit="10" d="M10.2,18 c0.8-3.9,3.9-7,7.8-7.8" strokeLinejoin="round" />
    </SvgWrapper>
  )
}

SearchSvg.defaultProps = defaultSvgProps
SearchSvg.propTypes = svgPropTypes

export const UpArrowSvg = ({ fill, height, width, stroke, strokeWidth, onClick }) => {
  return (
    <SvgWrapper height={height} width={width} onClick={onClick}>
      <polyline fill="none" stroke={stroke} strokeWidth={strokeWidth} strokeLinecap="round" strokeMiterlimit="10" points="44,34 24,14 4,34 " strokeLinejoin="round" />
    </SvgWrapper>
  )
}

UpArrowSvg.defaultProps = defaultSvgProps
UpArrowSvg.propTypes = svgPropTypes

export const DownArrowSvg = ({ fill, height, width, stroke, strokeWidth, onClick }) => {
  return (
    <SvgWrapper height={height} width={width} onClick={onClick}>
      <polyline fill="none" stroke={stroke} strokeWidth={strokeWidth} strokeLinecap="round" strokeMiterlimit="10" points="4,14 24,34 44,14 " strokeLinejoin="round" />
    </SvgWrapper>
  )
}

DownArrowSvg.defaultProps = defaultSvgProps
DownArrowSvg.propTypes = svgPropTypes

export const CheckSvg = ({ fill, height, width, stroke, strokeWidth, onClick }) => {
  return (
    <SvgWrapper height={height} width={width} onClick={onClick}>
      <path fill={fill} d="M2,23l4-4l9,6c0,0,10.025-11.408,29-17l2,4c0,0-20.962,9.025-31,28L2,23z" />
    </SvgWrapper>
  )
}

CheckSvg.defaultProps = defaultSvgProps
CheckSvg.propTypes = svgPropTypes



