import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import PostForm from '../PostForm'
import PanelTitle from './PanelTitle'
import Reviews from '../Reviews'

import { UpArrowSvg, DownArrowSvg } from '../../utils/svg'

export default class Panel extends React.Component {
  static propTypes = {
    getCenter: PropTypes.func,
    fetchGoogleMap: PropTypes.func,
    reviews: PropTypes.array,
    selectedReview: PropTypes.object,
    onSelectReview: PropTypes.func.isRequired,
    updateCenter: PropTypes.func.isRequired,
    fetchReviews: PropTypes.func.isRequired,
  }

  constructor() {
    super()
    this.state = {
      status: 'default',
      expanded: false,
      searching: false,
    }
    this.reviews = undefined
  }

  onSelectReview = (review) => {
    const { onSelectReview } = this.props
    onSelectReview(review)
    this.setState({
      status: 'selected',
      expanded: false,
    })
  }

  onRedoSearch = () => {
    const { fetchReviews } = this.props

    this.setState({
      searching: true,
    })
    fetchReviews().then(() => {
      this.setState({
        searching: false,
      })
    })
  }

  setReviews = (c) => {
    this.reviews = c
  }

  expand = () => {
    this.setState({
      expanded: true,
    })
  }

  shrink = () => {
    this.setState({
      expanded: false,
    })
  }

  goBrowse = () => {
    this.setState({
      status: 'default',
    }, () => {
      const { selectedReview } = this.props
      if (selectedReview) {
        this.scrollToReview(selectedReview)
      }
    })
  }

  goAdd = () => {
    this.setState({
      status: 'add',
    })
  }

  scrollToReview = (review) => {
    this.reviews.scrollToReview(review)
  }

  renderExpand = () => {
    const { expanded, status } = this.state
    if (status === 'selected' || status === 'add') return null

    return (
      <div className="expand">
        {expanded?
          <DownArrowSvg
            height={20}
            width={20}
            stroke="#666"
            strokeWidth={5}
            onClick={this.shrink}
          />
          :
          <UpArrowSvg
            height={20}
            width={20}
            stroke="#666"
            strokeWidth={5}
            onClick={this.expand}
          />
        }
      </div>
    )
  }

  renderNav = () => {
    const { status, searching } = this.state
    return (
      <div className="nav">
        <button className={classNames({ active: status === 'default' })} onClick={this.goBrowse}>Browse</button>
        <button className={classNames({ active: status === 'add' })} onClick={this.goAdd}>Submit a review</button>
        <button onClick={this.onRedoSearch}>{searching ? 'Searching ...' : 'Redo search'}</button>
      </div>
    )
  }

  render() {
    const { getCenter, fetchGoogleMap, reviews, selectedReview, updateCenter } = this.props
    const { expanded, status } = this.state

    return (
      <div className={classNames("panel", { [status]: true, expanded: expanded || status === 'add' })}>
        <PanelTitle />
        {this.renderNav()}
        {this.renderExpand()}
        {(status === 'default' || status === 'selected') && <Reviews
          ref={this.setReviews}
          getCenter={getCenter}
          reviews={reviews}
          onSelectReview={this.onSelectReview}
          selectedReview={selectedReview}
          updateCenter={updateCenter}
          showSelected={status === 'selected'}
          goAdd={this.goAdd}
          goBrowse={this.goBrowse}
        />}
        {status === 'add' && <PostForm getCenter={getCenter} fetchGoogleMap={fetchGoogleMap} />}
      </div>
    )
  }
}



