import React from 'react'
import PropTypes from 'prop-types'
import classNames from 'classnames'

import { Checkbox, CheckboxGroup } from 'react-checkbox-group'
import { RadioGroup, Radio } from 'react-radio-group'

import { camelCaseToUnderscore } from '../../utils/strings'
import { ThumbsDownSvg, ColoredFlameSvg, ThumbsUpSvg, FlameSvg } from '../../utils/svg'

const genderOptions = ['Male', 'Female', 'Other']
const ratingOptions = [
  {
    graphic: (checked) => {
      return (
        <ThumbsDownSvg
          height={32}
          width={32}
          fill={checked ? '#d44950' : '#ddd'}
        />
      )
    },
    value: 'bad',
  },
  {
    graphic: (checked) => {
      return (
        <ThumbsUpSvg
          height={32}
          width={32}
          fill={checked ? '#21b384' : '#ddd'}
        />
      )
    },
    value: 'good',
  },
  {
    graphic: (checked) => {
      if (checked) {
        return (
          <ColoredFlameSvg
            height={32}
            width={32}
            fill="#ddd"
          />
        )
      }

      return (
        <FlameSvg
          height={32}
          width={32}
          fill="#ddd"
        />
      )
    },
    value: 'awesome',
  },
]

const tagOptions = [
  'Hangout',
  'FirstDate',
  'RomanticDate',
  'MakingOut',
  'SexyTime',
  'Dancing',
  'PlayGames',
  'Anniversary',
]

export default class DetailsForm extends React.Component {
  static propTypes = {
    locationDescription: PropTypes.string,
    show: PropTypes.bool,
  }

  constructor() {
    super()
    this.state = {
      comment: '',
      partnerGender: '',
      userGender: '',
      rating: '',
      tags: [],
      hasError: false,
    }
  }

  onPartnerGenderChange = (value) => {
    this.setState({
      partnerGender: value,
    })
  }

  onUserGenderChange = (value) => {
    this.setState({
      userGender: value,
    })
  }

  onRatingChange = (value) => {
    this.setState({
      rating: value,
    })
  }

  onTagsChange = (value) => {
    this.setState({
      tags: value,
    })
  }

  onCommentChange = (e) => {
    const { value } = e.target
    if (value.length > 200) return

    this.setState({
      comment: value,
    })
  }

  validate = () => {
    const { rating, tags } = this.state
    const valid = rating !== '' && tags.length !== 0

    this.setState({
      hasError: !valid,
    })

    return valid
  }

  getState = () => {
    return this.state
  }

  renderGenderInput = () => {
    const { partnerGender, userGender } = this.state

    return (
      <div className="genderInput">
        <RadioGroup className="radioGroup outline half" name="userGender" selectedValue={userGender} onChange={this.onUserGenderChange}>
          <div className="title">Your gender</div>
          <div className="options pullLeft">
            {
              genderOptions.map((s) => {
                const value = s.toLowerCase()
                return (
                  <label key={value} className={classNames({ checked: userGender === value })}><Radio value={value} />{s}</label>
                )
              })
            }
          </div>
        </RadioGroup>
        <RadioGroup className="radioGroup outline half" name="partnerGender" selectedValue={partnerGender} onChange={this.onPartnerGenderChange}>
          <div className="title">Your partner's gender</div>
          <div className="options pullLeft">
            {
              genderOptions.map((s) => {
                const value = s.toLowerCase()
                return (
                  <label key={value} className={classNames({ checked: partnerGender === value })}><Radio value={value} />{s}</label>
                )
              })
            }
          </div>
        </RadioGroup>
      </div>
    )
  }

  renderRating = () => {
    const { rating } = this.state

    return (
      <div>
        <RadioGroup className="radioGroup rating" name="rating" selectedValue={rating} onChange={this.onRatingChange}>
          <div className="title">How was it?*</div>
          <div className="options">
            {
              ratingOptions.map((obj) => {
                const { graphic, value } = obj
                const checked = rating === value
                return (
                  <label key={value} className={classNames({ checked })}><Radio value={value} />{graphic && graphic(checked)}</label>
                )
              })
            }
          </div>
        </RadioGroup>
      </div>
    )
  }

  renderTags = () => {
    const { tags } = this.state

    return (
      <div>
        <CheckboxGroup className="checkboxGroup hashtags" name="tags" value={tags} onChange={this.onTagsChange}>
          <div className="title">What happened?*</div>
          <div className="options">
            {
              tagOptions.map((s) => {
                const lower = s.charAt(0).toLowerCase() + s.slice(1)
                const value = camelCaseToUnderscore(lower)
                // TODO use a Set to make more efficient
                const checked = tags.indexOf(value) !== -1
                return (
                  <label key={value} className={classNames({ checked })}>
                    <Checkbox value={value} checked={checked} />
                    #{s}
                  </label>
                )
              })
            }
          </div>
        </CheckboxGroup>
      </div>
    )
  }

  renderComment = () => {
    const { comment } = this.state

    return (
      <div className="comment">
        <div className="title">Give us some details ({200 - comment.length} characters left)</div>
        <textarea value={comment} onChange={this.onCommentChange} />
      </div>
    )
  }

  renderLocation = () => {
    const { locationDescription } = this.props
    return(
      <div className="location">
        <div className="title">Location*</div>
        {locationDescription}
      </div>
    )
  }

  render() {
    const { show } = this.props
    const { hasError } = this.state

    return (
      <div className={classNames("detailsForm", { show })}>
        <div className="formTitle">Tell us more</div>
        <section>
          {this.renderLocation()}
        </section>
        <section>
          {this.renderGenderInput()}
        </section>
        <section>
          {this.renderTags()}
        </section>
        <section>
          {this.renderRating()}
        </section>
        <section>
          {this.renderComment()}
        </section>
        {hasError && <div className="error">Please fill out the form</div>}
      </div>
    )
  }
}



